import React, { Component } from "react";
import './NavbarComponent.css';
import './NavbarComponent.scss';
import { Navbar, Nav, Dropdown, Button, Form, FormControl } from "react-bootstrap";
import ico from '../../Assets/bg1.jpg';
import { IoMdNotifications } from 'react-icons/io';
import { HiMail } from 'react-icons/hi';
import { RiHomeSmileFill, RiUser5Fill, RiSettings4Fill, RiLogoutCircleLine } from 'react-icons/ri';
import SidebarDisappearingComponent from "../SidebarDisappearing/SidebarDisappearingComponent";
import NotificationBar from "../SidebarDisappearing/NotificationBar";
import EmailBar from "../SidebarDisappearing/EmailBar";

export default class NavbarComponent extends Component {

    render() {
        return (
            <>
            
            <Navbar id="navbarTop" bg="light" sticky="top">
                <Navbar.Brand href="#home">myForment</Navbar.Brand>
                <Nav className="ml-auto">
                    
                    <EmailBar />

                    <NotificationBar />                    

                    <Dropdown>
                        <Dropdown.Toggle id="dropdown-basic" style={{ 'backgroundColor': 'none' }}>
                            <span style={{ 'verticalAlign': 'super' }}>Hi <strong>dellantonio47@gmail.com</strong></span>
                            <div className="MuiAvatar-root MuiAvatar-circle MuiAvatar-colorDefault">
                                <img src={ico} className="MuiIcon-root MuiAvatar-fallback" />
                            </div>
                        </Dropdown.Toggle>

                        <Dropdown.Menu>
                            <Dropdown.Item href="#/action-1"><RiHomeSmileFill/><span className="pl-2">Home</span></Dropdown.Item>
                            <Dropdown.Item href="#/action-2"><RiUser5Fill/><span className="pl-2">Profile</span></Dropdown.Item>
                            <Dropdown.Item href="#/action-3"><RiSettings4Fill/><span className="pl-2">Settings</span></Dropdown.Item>
                            <Dropdown.Item href="#/action-3"><RiLogoutCircleLine/><span className="pl-2">Logout</span></Dropdown.Item>
                        </Dropdown.Menu>
                    </Dropdown>
                </Nav>
            </Navbar>
            
            </>
        );
    }

}