import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom"
import AuthRoute from "./AuthRoute";
import Page1 from "../Pages/Page_1/Page1";
import Dashboard from "../Pages/Dashboard/Dashboard";
import NotFound from "../Pages/NotFound/NotFound";
import SidebarComponent from "../Components/Sidebar/SidebarComponent";
import NavbarComponent from '../Components/Navbar/NavbarComponent';
import LoginComponent from "../Pages/Authentication/LoginComponent";
import { FaBars } from 'react-icons/fa';


//Componente principale del progetto.
export default class MyformentApp extends Component {

    //State pe salvare i diversi tipi di ruoli
    state = {
        Basic: "ROLE_BASIC",
        Admin: 'ROLE_ADMIN',
        Editor: 'ROLE_EDITOR',


        collapsed: sessionStorage.getItem('SidebarCollapsed') === "true" ? true : false,
        image: sessionStorage.getItem('SidebarImage') !== "false" ? true : false,
        toggled: false
    }

    handleCollapsedChange = (checked) => {
        this.setState({ collapsed: checked });
        sessionStorage.setItem('SidebarCollapsed', checked);
    };

    handleImageChange = (checked) => {
        this.setState({ image: checked });
        sessionStorage.setItem('SidebarImage', checked);
    };

    handleToggleSidebar = (value) => {
        this.setState({ toggled: value });
    };

    render() {
        return (

            //Il router permette di attribuire un path ad una pagina e di limitarne l'accesso in base al ruolo
            //In base alla posizione che voglio che abbiano, chiamo anche i componenti di header e di footer

            //Div principale di tutta l'applicazione
            <div className="myformentApp">

                {/* Router utilizzato per navigare tra le varie pagine*/}
                <Router>

                    <Switch>

                        <Route path="/login" component={LoginComponent} />
                        <Route path="/" exact component={LoginComponent} />

                        {/* Percorsi interni alla struttura navbar + sidebar + contenuto */}
                        <Route>


                            {/* Div per il toggle della sidebar */}
                            <div className={`app ${this.state.toggled ? 'toggled' : ''}`}>

                                {/* Sidebar fissa a sinistra */}
                                <SidebarComponent
                                    image={this.state.image}
                                    collapsed={this.state.collapsed}
                                    toggled={this.state.toggled}
                                    handleCollapsedChange={this.handleCollapsedChange}
                                    handleImageChange={this.handleImageChange}
                                    handleToggleSidebar={this.handleToggleSidebar}
                                />



                                {/* Contenuto principale. Comprende la navbar e il corpo dove tutti i contenuti vengono visualizzati */}
                                <main>

                                    {/* Div di navigazione con il toggle per la sidebar quando su dispositivi mobili viene nascosta e la navbar top */}
                                    <div id="navigation">
                                        <div className="btn-toggle" onClick={() => this.props.handleToggleSidebar(true)}>
                                            <FaBars />
                                        </div>
                                        <NavbarComponent />
                                    </div>


                                    {/* Div del contenuto principale. Qui viene visualizzato il contenuto delle pagine. Lo switch permette di navigare tra le route */}
                                    <div id="content">
                                        <Route render={(props) => (
                                            <Switch>

                                                {/* Route della dasboard principale. Passo anche le props */}
                                                <AuthRoute path="/dashboard" role={this.state.Basic} exact component={() => <Dashboard
                                                    image={this.state.image}
                                                    collapsed={this.state.collapsed}
                                                    toggled={this.state.toggled}
                                                    handleCollapsedChange={this.handleCollapsedChange}
                                                    handleImageChange={this.handleImageChange}
                                                    handleToggleSidebar={this.handleToggleSidebar}
                                                />} />

                                                <Route path="/page-1" component={Page1} />


                                                {/* Route dell'errore 404 */}
                                                <Route component={NotFound} />
                                            </Switch>
                                        )} />
                                    </div>

                                </main>

                            </div>
                        </Route>
                    </Switch>
                </Router>

            </div>

        );
    }

}