import React, { Component } from "react";
import { Redirect } from "react-router-dom"

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { withStyles } from "@material-ui/core/styles";

import AuthenticationService from '../../Services/Authentication/authService.js';

const styles = theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    }
});


class LoginComponent extends Component {

    state = {
        userId: 'Roberto',
        password: '123Stella',
        rememberMe: false,
        isLogged: false,
        noLogged: false,
        username: null,
        token: null,
        ErrMsg: null
    }

    componentDidMount = () => {
        try {
            this.setState({
                token: AuthenticationService.getTokenFromCookie(),
                username: AuthenticationService.getUserInfo()
            });
        } catch (e) {
            console.log(e);
        }
    }

    render() {

        const { classes } = this.props;

        if (this.state.token === null || this.state.username === null || this.state.token === '' || this.state.username === '') {

            return (

                <Container component="main" maxWidth="xs">
                    <CssBaseline />
                    <div className={classes.paper}>
                        <Avatar className={classes.avatar}>
                            <LockOutlinedIcon />
                        </Avatar>
                        <Typography component="h1" variant="h5">
                            Sign in
                        </Typography>

                        {this.state.ErrMsg && <div className="alert alert-danger"><h5>{this.state.ErrMsg}</h5></div>}

                        <form className={classes.form} noValidate onSubmit={this.Login}>
                            <TextField
                                variant="outlined"
                                margin="normal"
                                required
                                fullWidth
                                id="userId"
                                label="Email Address"
                                name="userId"
                                autoComplete="userId"
                                autoFocus
                                defaultValue={this.state.userId}
                                onChange={this.GestMod}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                required
                                fullWidth
                                name="password"
                                label="Password"
                                type="password"
                                id="password"
                                autoComplete="current-password"
                                defaultValue={this.state.password}
                                onChange={this.GestMod}
                            />
                            <FormControlLabel
                                control={<Checkbox value="remember" color="primary" id="rememberMe" name="rememberMe" onChange={this.GestMod} />}
                                label="Remember me"
                            />
                            <Button
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                className={classes.submit}
                            >
                                Sign In
                        </Button>
                            <Grid container>
                                <Grid item xs>
                                    <Link href="#" variant="body2">
                                        Forgot password?
                                </Link>
                                </Grid>
                                <Grid item>
                                    <Link href="#" variant="body2">
                                        {"Don't have an account? Sign Up"}
                                    </Link>
                                </Grid>
                            </Grid>
                        </form>
                    </div>
                    <Box mt={8}>
                        {() => this.Copyright}
                    </Box>
                </Container>

            );
        } else {
            return <Redirect to={"/dashboard"} />
        }
    }

    //Metodo per il login.
    Login = (event) => {
        event.preventDefault(); //Importante percè essendo il login una form, evita che premendo invio la pagina si refreshi.

        //Chiamo la funzione che contatta l'api backend per il login e la generazione del token JWT
        AuthenticationService.JWTAuthServer(this.state.userId, this.state.password, this.state.rememberMe)
            .then((response) => {
                //Non serve fare altro perchè l'API crea un cookie
                this.props.history.push(`/welcome/${this.state.userId}`); //Reindirizzo nella pagina di welcome
            })
            .catch((error) => {
                this.handleError(this, error)
                //Resetto le variabili dello state se trovo un problema
                this.setState({
                    isLogged: false,
                    noLogged: true
                })
            })
    }

    //Metodo che aggiorna le variabili dello state quando vengono modificati i dati all'interno della form
    GestMod = (event) => {

        if (event.target.type === "checkbox") {
            this.setState({
                [event.target.name]: event.target.checked
            })
        } else if (event.target.type === "text" || event.target.type === "password") {
            this.setState({
                [event.target.name]: event.target.value
            })
        } else {
            console.log(event.target.type + " -> " + event.target.name + ": " + event.target.value);
        }
    }


    Copyright = () => {
        return (
            <Typography variant="body2" color="textSecondary" align="center">
                {'Copyright © '}
                <Link color="inherit" href="https://material-ui.com/">
                    Your Website
            </Link>{' '}
                {new Date().getFullYear()}
                {'.'}
            </Typography>
        );
    }


    handleError = (obj, error) => {
        if (error.response.status === 403) {
            obj.setState({
                ErrMsg: "Non hai il permesso di eseguire questa funzionaltà",
                ErrWebApi: true
            })
        } else {
            obj.setState({
                ErrMsg: error.response.data.message,
                ErrWebApi: true
            })
        }
    }


}



const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    }
}));


export default withStyles(styles, { withTheme: true })(LoginComponent);
