import React, { Component } from "react";
import Switch from 'react-switch';
import { Link } from 'react-router-dom';
import SidebarItems from "./SidebarItems";
import {
    ProSidebar,
    Menu,
    MenuItem,
    SubMenu,
    SidebarHeader,
    SidebarFooter,
    SidebarContent,
} from 'react-pro-sidebar';
import "./SidebarComponent.css"
import { FaTachometerAlt, FaGem, FaList, FaGithub, FaRegLaughWink, FaHeart } from 'react-icons/fa';
import sidebarBg from '../../Assets/bg1.jpg';

export default class SidebarComponent extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <ProSidebar
                image={this.props.image ? sidebarBg : false}
                collapsed={this.props.collapsed}
                toggled={this.props.toggled}
                breakPoint="md"
                onToggle={this.props.handleToggleSidebar}
            >
                <SidebarHeader>
                    <div
                        style={{
                            padding: '24px',
                            textTransform: 'uppercase',
                            fontWeight: 'bold',
                            fontSize: 14,
                            letterSpacing: '1px',
                            overflow: 'hidden',
                            textOverflow: 'ellipsis',
                            whiteSpace: 'nowrap',
                        }}
                    >
                        Pro Sidebar
        </div>
                </SidebarHeader>

                <SidebarContent>
                    {
                        SidebarItems.map((item, index) => {
                            return (
                                <Menu key={index} iconShape="circle">
                                    <Link to={item.route}>
                                        <MenuItem
                                            key={item.name} active={''/*index === activeIndex*/}
                                            icon={<FaTachometerAlt />}
                                            suffix={<span className="badge red">NEW</span>}
                                        >
                                            {item.name}
                                        </MenuItem>
                                    </Link>
                                </Menu>
                            );
                        })
                    }
                </SidebarContent>

                <SidebarFooter style={{ textAlign: 'center' }}>
                    <div className={`block switch${this.props.collapsed ? '-collapsed' : ''}`}>
                        <Switch
                            height={16}
                            width={30}
                            checkedIcon={false}
                            uncheckedIcon={false}
                            onChange={this.props.handleCollapsedChange}
                            checked={this.props.collapsed}
                            onColor="#219de9"
                            offColor="#bbbbbb"
                        />
                        <span> collapsed </span>
                    </div>
                    <div className={`block switch${this.props.collapsed ? '-collapsed' : ''}`}>
                        <Switch
                            height={16}
                            width={30}
                            checkedIcon={false}
                            uncheckedIcon={false}
                            onChange={this.props.handleImageChange}
                            checked={this.props.image}
                            onColor="#219de9"
                            offColor="#bbbbbb"
                        />
                        <span> image </span>
                    </div>
                    <div
                        className="sidebar-btn-wrapper"
                        style={{
                            padding: '20px 24px',
                        }}
                    >

                        <a
                            href="https://github.com/azouaoui-med/react-pro-sidebar"
                            target="_blank"
                            className="sidebar-btn"
                            rel="noopener noreferrer"
                        >
                            <FaGithub />
                            <span> viewSource </span>
                        </a>
                    </div>
                </SidebarFooter>
            </ProSidebar>
        );
    }

}