import React from 'react';
import "./SidebarDisappearingComponent.scss";
import { Drawer, Button } from '@material-ui/core';
import { Nav } from "react-bootstrap";
import NotificationCard from "../Cards/NoticationCards";


export default function TemporaryDrawer(props) {

  const {
    position,
    title,
    iconComponent,
    alertList
  } = props;

  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  return (

    <div style={{ height: 'fit-content', margin: 'auto' }}>
      <React.Fragment key={position}>
        <Nav.Link onClick={toggleDrawer(position, true)}>
          <span>
            <span className="MuiBadge-root">
              <span>{iconComponent}</span>
              <span className="MuiBadge-badge MuiBadge-anchorOriginTopRightRectangle MuiBadge-colorSecondary">3</span>
            </span>
          </span>
        </Nav.Link>

        <Drawer anchor={position} open={state[position]} onClose={toggleDrawer(position, false)}>

          <div id="barHeader" className="elevation-z6 p-3 mb-3 items-center">
            <div className="barHeaderContent">
              <span className="defaultIcon">{iconComponent}</span>
              <h5 className="ml-2 my-0 font-medium inline">{title}</h5>
            </div>
          </div>


          {(typeof alertList != "undefined" && alertList != null && alertList.length != null && alertList.length > 0) ? (
            <div id="barContent">

              {alertList.map((alert, index) =>
                <NotificationCard key={index} title={alert.title} text={alert.text} isClosable={true} image=":)" />
              )}

              <div className="text-center">
                <Button>Clear All</Button>
              </div>

            </div>


          ) : (
              <div id="barContent">
                <NotificationCard title="Tutto in ordine :D" text={undefined} isClosable={false} image={undefined} />
              </div>
            )
          }


        </Drawer>
      </React.Fragment>
    </div>

  );
}